import { Component, OnDestroy, OnInit } from '@angular/core';

import { Store } from '@ngrx/store';
import { EChartOption } from 'echarts';
import { BasePageComponent } from '../../base-page';
import { IAppState } from '../../../interfaces/app-state';
import { HttpService } from '../../../services/http/http.service';
import {ActivatedRoute, Router} from "@angular/router";
import { DatePipe } from '@angular/common';
import { environment } from '../../../env';
@Component({
  selector: 'page-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class PageDashboardComponent extends BasePageComponent implements OnInit, OnDestroy {
  hsOptions: EChartOption;
  paOptions: EChartOption;
  pgOptions: EChartOption;
  dOptions: EChartOption;
  piOptions: EChartOption;
  heOptions: EChartOption;
  appointments: any[];
  piePatternSrc: string;
  piePatternImg: any;
  pieStyle: any;
  totalrevenue: any;
  totalpatient: any;
  patientpending: any;
  patientapprove: any;
  lastappoint:  any;
  profileName:  any; 
  pievalue1:  any;
  pievalue2:  any;
  pievalue3:  any;
  pievalue4:  any;
  pievalue5:  any;
  prevyear :  any;
  currentyear:  any;
  startDate:  any;
  endDate:  any;
  week: any;
  weekrevenue: any;
  monthrevenue: any;
  hospitalId: any;
  public API_URL:any = environment.backend;
  constructor(
    store: Store<IAppState>,
    httpSv: HttpService,
    private router: Router,
    private datePipe : DatePipe
  ) {
    super(store, httpSv);

    this.pageData = {
      title: '',
      loaded: false,
      breadcrumbs: [
        {
          title: 'Dashboards',
          route: 'default-dashboard'
        },
        {
          title: 'Default'
        }
      ]
    };
    this.appointments = [];
    this.piePatternSrc = 'data:image/jpeg;base64,';
    this.piePatternImg = new Image();
    this.piePatternImg.src = this.piePatternSrc;
    this.pieStyle = {
      normal: {
        opacity: 0.7,
        color: {
          image: this.piePatternImg,
          repeat: 'repeat'
        },
        borderWidth: 3,
        borderColor: '#336cfb'
      }
    };
  }

  ngOnInit() {
    super.ngOnInit();
    this.hospitalId =JSON.parse(localStorage.getItem('user')).id;
    var data = localStorage.getItem('user');
    if(data ==null){
      this.router.navigateByUrl('/public/sign-in');
    }
    var parseData = JSON.parse(data);
    this.profileName =parseData.username;
    //  this.getData(this.API_URL+'listAppointment', 'appointments', 'setLoaded');
   
    this.getData(this.API_URL+'payments-get/'+this.hospitalId, 'payments', 'setLoaded');
    this.httpSv.lastappintment(this.API_URL+'listAppointment/'+this.hospitalId).subscribe(response => {
      this.lastappoint= response.slice(Math.max(response.length - 10, 0));
    
    });
    this.httpSv.getPatient(this.API_URL+'patient-get/'+this.hospitalId).subscribe(response => {
      this.currentyear = response.filter(function(val,key){return (val.lastVisit.search('2020') !=-1)});
      this.prevyear = response.filter(function(val,key){return (val.lastVisit.search('2019') !=-1)})
      this.pievalue1 = response.filter(function(val,key){return (val.age<10 && 0<val.age )}).length;
      this.pievalue2 = response.filter(function(val,key){return (val.age<20 && 10<val.age )}).length;
      this.pievalue3 = response.filter(function(val,key){return (val.age<30 && 20<val.age )}).length;
      this.pievalue4 = response.filter(function(val,key){return (val.age<40 && 30<val.age )}).length;
      this.pievalue5 = response.filter(function(val,key){return ( 40<val.age )}).length;
      this.totalpatient= response.length;
      this.setPGOptions(response.filter(function(val,key){return (val.gender == 'male' )}).length,response.filter(function(val,key){return (val.gender == 'female' )}).length);
      this.setPAOptions(this.pievalue1,this.pievalue2,this.pievalue3,this.pievalue4,this.pievalue5);
      this.setHSOptions(this.prevyear,this.currentyear);
    });
    this.httpSv.pendingPatient(this.API_URL+'patients-status-pending/'+this.hospitalId).subscribe(response => {
      this.patientpending= response.length;
    });
    this.httpSv.approvepatient(this.API_URL+'patients-status-approved/'+this.hospitalId).subscribe(response => {
      this.patientapprove= response.length;
    });
    this.httpSv.getdoctors(this.API_URL+'doctors/'+this.hospitalId).subscribe(response => {
      // this.setDOptions(response);
      this.setDOptions(response);
    });
    this.httpSv.getpayment(this.API_URL+'payments-get/'+this.hospitalId).subscribe(response => {
      this.totalrevenue = response.reduce((a, {total}) => a + Number(total), 0);
     var filterWeek = []
     var filtermonth = [];
     response.forEach(function(val,key){
        var current = new Date();     // get current date
        var weekstart = current.getDate() - current.getDay() +1;    
        var weekend = weekstart + 6;       // end day is the first day + 6
        var startDate = new Date(current.setDate(weekstart)).setHours(0,0,0,0);  
        var endDate = new Date(current.setDate(weekend)).setHours(0,0,0,0);
        if((((new Date(startDate) <= new Date(val.billDate))  &&  (new Date(val.billDate) <= new Date(endDate) )))){
          filterWeek.push(Number(val.total)) 
        }
        });
          this.week = filterWeek;
          this.setPIOptions(this.week);
          const reducer = (accumulator, currentValue) => accumulator + currentValue;
          this.weekrevenue = filterWeek.reduce(reducer)
         console.log(this.week);

         response.forEach(function(val,key){
          var date = new Date();
          var firstDay = new Date(date.getFullYear(), date.getMonth(), 1).setHours(0,0,0,0);
          var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0).setHours(0,0,0,0);
          if((((new Date(firstDay) <= new Date(val.billDate))  &&  (new Date(val.billDate) <= new Date(lastDay) )))){
            filtermonth.push(Number(val.total)) 
          }
          });

          this.setHEOptions(filtermonth);
          const reducer1 = (accumulator, currentValue) => accumulator + currentValue;
          this.monthrevenue = filtermonth.reduce(reducer1)
      // this.startDate = new Date(new Date().setDate(new Date().getDate()-7));
      // this.endDate = new Date();
    });
   
    
    
    // this.setPIOptions();
    // this.setHEOptions();
  }

  ngOnDestroy() {
    super.ngOnDestroy();
  }

  setHSOptions(a,b) {
    this.hsOptions = {
      color: ['#ed5564', '#336cfb'],
      tooltip: {
        trigger: 'none',
        axisPointer: {
          type: 'cross'
        }
      },
      legend: {
        data: ['Patients 2019', 'Patients 2020']
      },
      grid: {
        left: 30,
        right: 0,
        top: 50,
        bottom: 50
      },
      xAxis: [
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: '#336cfb'
            }
          },
          axisPointer: {
            label: {
              formatter: function (params) {
                return 'Patients ' + params.value
                  + (params.seriesData.length ? '：' + params.seriesData[0].data : '');
              }
            }
          },
          data: ['2020-1', '2020-2', '2020-3', '2020-4', '2020-5', '2020-6', '2020-7', '2020-8', '2020-9', '2020-10', '2020-11', '2020-12']
        },
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: '#ed5564'
            }
          },
          axisPointer: {
            label: {
              formatter: function (params) {
                return 'Patients ' + params.value
                  + (params.seriesData.length ? '：' + params.seriesData[0].data : '');
              }
            }
          },
          data: ['2019-1', '2019-2', '2019-3', '2019-4', '2019-5', '2019-6', '2019-7', '2019-8', '2019-9', '2019-10', '2019-11', '2019-12']
        }
      ],
      yAxis: [
        {
          type: 'value'
        }
      ],
      series: [
        {
          name: 'Patients 2019',
          type: 'line',
          xAxisIndex: 1,
          smooth: true,
          data: [a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '01')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '02')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '03')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '04')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '05')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '06')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '07')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '08')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '09')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '10')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '11')}).length, a.filter(function(val,key){return (val.lastVisit.split('-')[1] == '12')}).length]
        },
        {
          name: 'Patients 2020',
          type: 'line',
          smooth: true,
          data: [b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '01')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '02')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '03')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '04')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '05')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '06')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '07')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '08')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '09')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '10')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '11')}).length, b.filter(function(val,key){return (val.lastVisit.split('-')[1] == '12')}).length]
        }
      ]
    };
  }

  setPAOptions(a,b,c,d,e) {
    console.log(a);
    console.log(b)
    this.paOptions = {
      grid: {
        left: 0,
        right: 0,
        top: 0,
        bottom: 0
      },
      tooltip : {
        trigger: 'item',
        formatter: '{b}<br>{c} ({d}%)'
      },
      series: [{
        name: 'pie',
        type: 'pie',
        selectedMode: 'single',
        selectedOffset: 30,
        clockwise: true,
        radius: [60, '90%'],
        label: {
          normal: {
            position: 'inner',
            textStyle: {
              fontSize: 14,
              fontWeight: 700,
              color: '#000'
            }
          }
        },
        labelLine: {
          normal: {
            lineStyle: {
              color: '#336cfb'
            }
          }
        },
        data:[
          { value: a, name: '0-10' },
          { value: b, name: '10-20' },
          { value: c, name: '20-30' },
          { value: d, name: '30-40' },
          { value: e, name: '40+' }
        ],
        itemStyle: this.pieStyle
      }]
    };
  }

  setPGOptions(a,b) {
    this.pgOptions = {
      grid: {
        left: 0,
        right: 0,
        top: 0,
        bottom: 0
      },
      tooltip : {
        trigger: 'item',
        formatter: '{b}<br>{c} ({d}%)'
      },
      series: [{
        name: 'pie',
        type: 'pie',
        selectedMode: 'single',
        selectedOffset: 30,
        clockwise: true,
        radius: [0, '90%'],
        label: {
          normal: {
            position: 'inner',
            textStyle: {
              fontSize: 14,
              fontWeight: 700,
              color: '#000'
            }
          }
        },
        labelLine: {
          normal: {
            lineStyle: {
              color: '#336cfb'
            }
          }
        },
        data:[
          { value: a, name: 'Female' },
          { value: b, name: 'Male' }
        ],
        itemStyle: this.pieStyle
      }]
    };
  }

  setDOptions(res) {
    this.dOptions = {
      grid: {
        left: 0,
        right: 0,
        top: 0,
        bottom: 0
      },
      tooltip : {
        trigger: 'item',
        formatter: '{b}<br>{c} ({d}%)'
      },
      series: [{
        name: 'pie',
        type: 'pie',
        radius: [0, '90%'],
        roseType : 'area',
        label: {
          normal: {
            show: false
          }
        },
        data:[
          { value:  res.filter(function(val,key){return (val.role == "Cardiologist")}).length, name: "Cardiologist" },
          { value: res.filter(function(val,key){return (val.role == 'Anesthesiologist')}).length, name: 'Anesthesiologist' },
          { value: res.filter(function(val,key){return (val.role == 'Dermatologist')}).length, name: 'Dermatologist' },
          { value: res.filter(function(val,key){return (val.role == 'Internist')}).length, name: 'Internist' },
          { value: res.filter(function(val,key){return (val.role == 'Obstetrician')}).length, name: 'Obstetrician' }
        ],
        itemStyle: this.pieStyle
      }]
    };
  }

  setPIOptions(weekdata) {
    this.piOptions = {
      color: ['#ed5564'],
      grid: {
        left: 0,
        right: 0,
        top: 0,
        bottom: 0
      },
      xAxis: {
        boundaryGap: false,
        type: 'category'
      },
      yAxis: {
        show: false
      },
      series: [
        {
          name: 'Patients 2019',
          type: 'line',
          smooth: true,
          data: weekdata,
          areaStyle: {}
        }
      ]
    };
  }

  setHEOptions(monthdata) {
    this.heOptions = {
      color: ['#64B5F6'],
      grid: {
        left: 0,
        right: 0,
        top: 0,
        bottom: 0
      },
      xAxis: {
        boundaryGap: false,
        type: 'category'
      },
      yAxis: {
        show: false
      },
      series: [
        {
          name: 'Patients 2019',
          type: 'line',
          smooth: true,
          data: monthdata,
          areaStyle: {}
        }
      ]
    };
  }
}
